import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button, message } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import { BtnAddNew, BtnExport } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import {
  getDormRoomStudents,
  deleteDormRoomStudents,
  deleteOutRoomStudents,
  exportDormRoomStudents,
} from 'services/inOutStudent';
import { dateToString } from 'utils/formatUtil';
import FormSearch from './FormSearch';
import './style.scss';

class InOutStudentPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleXoaNoiTru = async id => {
    try {
      await deleteDormRoomStudents(id);
      message.success('Xóa thành công');
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleXoaNgoaiTru = async id => {
    try {
      await deleteOutRoomStudents(id);
      message.success('Xóa thành công');
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({ trainTypeId, facultyId, courseId, studyFieldId, classId }) => {
    try {
      await exportDormRoomStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã sv',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Ngày bắt đầu',
        dataIndex: 'startDate',
        render: (text, record) => {
          if (record.startDate == null) {
            return <Fragment />;
          }
          return <Fragment>{dateToString(record.startDate, 'DD/MM/YYYY')}</Fragment>;
        },
      },
      {
        title: 'Phòng nội trú',
        dataIndex: 'dormRoomName',
        key: 'dormRoomName',
      },
      {
        title: 'Địa chỉ ngoại trú',
        width: '140px',
        dataIndex: 'outRoomAddress',
        key: 'outRoomAddress',
      },
      {
        title: 'Ghi chú',
        width: '140px',
        dataIndex: 'note',
        key: 'note',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => {
          if (record.dormRoom_StudentId) {
            return (
              <Fragment>
                <Button
                  className="btnEventStuPartyMem btnKhaiTru"
                  type="primary"
                  onClick={() => this.handleXoaNoiTru(record.dormRoom_StudentId)}
                >
                  Xóa nội trú
                </Button>
              </Fragment>
            );
          }
          if (record.outRoom_StudentId) {
            return (
              <Fragment>
                <Button
                  className="btnEventStuPartyMem btnKhaiTru"
                  type="primary"
                  onClick={() => this.handleXoaNgoaiTru(record.outRoom_StudentId)}
                >
                  Xóa ngoại trú
                </Button>
              </Fragment>
            );
          }
          return (
            <div>
              <BtnAddNew title="Nội trú" href={`#/in-student/form/${record.studentId}`} />
              <BtnAddNew title="Ngoại trú" href={`#/out-student/form/${record.studentId}`} />
            </div>
          );
        },
      },
    ];
    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Sinh viên nội trú, ngoại trú">
        <Row>
          <CartHeadAction>
            <BtnExport
              title="Xuất Excel"
              onClick={() =>
                this.handleExport({
                  trainTypeId: listParamSearch.trainTypeId,
                  facultyId: listParamSearch.facultyId,
                  courseId: listParamSearch.courseId,
                  studyFieldId: listParamSearch.studyFieldId,
                  classId: listParamSearch.classId,
                })
              }
            />
          </CartHeadAction>
          <Col span={24}>
            <FormSearch />
          </Col>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getDormRoomStudents}
              allowGetData={
                !!listParamSearch.trainTypeId &&
                !!listParamSearch.facultyId &&
                !!listParamSearch.courseId
              }
              columns={columns}
              scroll={{ x: 'true' }}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <InOutStudentPage />
  </Page>
);



